import React from 'react';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';

import { withStyles } from '@material-ui/core/styles';
import { BrowserRouter as Router, Route, Redirect, Switch } from "react-router-dom";
import VolunteerAppBar from './containers/VolunteerAppBar'

import MyShifts from './screens/MyShifts';
import MyDetails from './screens/MyDetails';
import ChangePassword from './screens/ChangePassword';
import ManageAvailabiliy from './screens/ManageAvailability'

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#FCFCFC'
    }
  },
  typography: {
    fontFamily: '"Helvetica Neue",Helvetica,Arial,sans-serif',
    useNextVariants: true,
  }
});

const styles = theme => ({
  root: {
    display: 'flex',
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    [theme.breakpoints.up('sm')]: {
      padding: theme.spacing.unit * 3
    },
  },
});

function App(props) {
    let {classes}=props;
    return (
      <MuiThemeProvider theme={theme}>
        <Router>
          <div className={classes.root}>
            <CssBaseline />
            <VolunteerAppBar />
            <main className={classes.content}>
              <div className={classes.toolbar} />
              <Switch>
                <Route path="/account/my-shifts" component={MyShifts}/>
                <Route path="/account/manage-availability" component={ManageAvailabiliy}/>
                <Route path="/account/my-details" component={MyDetails}/>
                <Route path="/account/change-password" component={ChangePassword}/>
                <Redirect from='/' to='/account/my-shifts'/>
              </Switch>
            </main>
          </div>
        </Router>
        
      </MuiThemeProvider>
    );
}

export default withStyles(styles)(App);
