import React from 'react';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import MenuItem from '@material-ui/core/MenuItem';
import { Link } from "react-router-dom";

function NavigationDrawerItem(props) {
    const {to, onClick, text, icon, selected} = props;

    let itemIcon = icon !== undefined ? <ListItemIcon>{icon}</ListItemIcon> : null;

    return (
        <Link to={to} style={{textDecoration: 'none'}}>
            <MenuItem onClick={onClick} selected={selected}>
                {itemIcon}
                <ListItemText primary={text} />
            </MenuItem>
        </Link>
    )
}

export default NavigationDrawerItem;