import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Hidden from '@material-ui/core/Hidden';
import NavigationDrawerItem from '../components/NavigationDrawerItem'
import NavigationDrawer from '../components/NavigationDrawer'
import Divider from '@material-ui/core/Divider';

import Work from '@material-ui/icons/Work';
import CalendarToday from '@material-ui/icons/CalendarToday';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Lock from '@material-ui/icons/Lock';
import ExitToApp from '@material-ui/icons/ExitToApp';

const styles = theme => ({
    root: {
        display: 'flex',
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
    }
});

class VolunteerAppBar extends React.Component {
    
    constructor(props) {
        super(props);

        this.state = {
            openMobileDrawer: false
        }

        this.closeMobileDrawer = this.closeMobileDrawer.bind(this);
    }

    closeMobileDrawer() {
        this.setState({openMobileDrawer: false});
    }

    render() {

        let {classes} = this.props;

        return (
            <React.Fragment>
                
                <AppBar position="fixed" className={classes.appBar}>
                    <Toolbar>
                        <Hidden smUp implementation="css">
                            <IconButton color="inherit" aria-label="Menu" onClick={() => {this.setState({openMobileDrawer: !this.state.openMobileDrawer})}}>
                                <MenuIcon />
                            </IconButton>
                        </Hidden>
                        <Typography variant="h6" color="inherit">
                            Volunteer Edinburgh
                        </Typography>
                        
                    </Toolbar>
                </AppBar>
                <NavigationDrawer openMobileDrawer={this.state.openMobileDrawer} onCloseMobileDrawer={() => this.setState({openMobileDrawer: false})}>
                    <NavigationDrawerItem to="/account/my-shifts" text="My Shifts" icon={<Work />} onClick={this.closeMobileDrawer}/>
                    <NavigationDrawerItem to="/account/manage-availability" text="Manage Availability" icon={<CalendarToday />} onClick={this.closeMobileDrawer}/>
                    <NavigationDrawerItem to="/account/my-details" text="My Details" icon={<AccountCircle />} onClick={this.closeMobileDrawer}/>
                    <NavigationDrawerItem to="/account/change-password" text="Change Password" icon={<Lock />} onClick={this.closeMobileDrawer}/>
                    <Divider />
                    <NavigationDrawerItem to="/account/signout" text="Sign Out" icon={<ExitToApp />}/>
                </NavigationDrawer>
            </React.Fragment>
        )
    }
}

export default withStyles(styles)(VolunteerAppBar);